import React, {
	forwardRef, ReactNode, useEffect, useRef,
} from "react";

import { InputBase } from "../Input/Input.types";
import { Margins } from "../styleguide";
import uuid from "../utils/uuid";

import {
	CheckboxContainer,
	CheckboxInput,
	CheckMarkContainer,
	Label,
	StyledCheckboxProps,
} from "./Checkbox.styles";
import CheckDash from "./CheckDash";
import CheckMark from "./CheckMark";

export interface CheckboxProps extends StyledCheckboxProps, InputBase, Margins {
	label?: string | ReactNode | ReactNode[],
}


const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>((props, ref) => {
	const {
		dataQA,
		id,
		label,
		onChange = () => { },
		disabled,
		isInvalid = false,
		className,
		checked,
		m,
		mt,
		mb,
		mr,
		ml,
		isIndeterminate,
	} = props;

	const checkBoxRef = useRef<HTMLInputElement>(null);
	const forLabel = id !== undefined ? id : uuid();

	useEffect(() => {
		if (checkBoxRef && checkBoxRef.current) {
			checkBoxRef.current.indeterminate = isIndeterminate || false;
		}
	}, [checkBoxRef, isIndeterminate]);

	return (
		<CheckboxContainer
			className={className}
			data-qa={dataQA}
			m={m}
			mt={mt}
			mb={mb}
			mr={mr}
			ml={ml}
			ref={ref}
		>
			<CheckboxInput
				ref={checkBoxRef}
				data-qa={`${dataQA}-input`}
				type="checkbox"
				onChange={onChange}
				isInvalid={isInvalid}
				checked={checked}
				id={forLabel}
				name={forLabel}
				{...props}
			/>
			{isIndeterminate && (
				<CheckMarkContainer>
					<CheckDash />
				</CheckMarkContainer>
			)}
			{checked && (
				<CheckMarkContainer>
					<CheckMark />
				</CheckMarkContainer>
			)}

			{label && <Label data-qa={`${dataQA}-label`} disabled={disabled} htmlFor={forLabel}>{label}</Label>}
		</CheckboxContainer>
	);
});

Checkbox.displayName = "Checkbox";

export default Checkbox;
